@include formobile() {
	.mid-section {
		ul.nav {
			height: 0;
			overflow: hidden;
			@include transitionspeed(.3s);
			display: block;
			margin: 0;
			float: none;
			clear: both;

			li {
				float: none;
				//display: block
				border-bottom: 1px $darkGray solid;
				margin: 0;
				&:first-of-type {
					border-top: 1px $darkGray solid; }

				a {
					line-height: 30px;
					display: block;
					padding: 5px 10px 0;
					background: $darkGrayDarken;

					&.active,
					&:focus {
						background: $red;
						color: $white; } } }

			&.show {
				margin: 0;
				clear: both;
				width: 100%;
				@include transitionspeed(.3s); } } }
	.footer {
		ul.ftrLinks {
			li {
				margin-right: 0; } } }

	.fullCarouselBlock {
		.fullCarousel {
			.fc-content {
				.imgCaption {
					.b1000px {
						padding: 0 30px; }
					.tag {
						a {
							font-size: 12px; } }
					h1 {
						font-size: 21px; }
					p {
						font-size: 14px;
						line-height: 21px; }
					@include forstablet() {
						.tag {
							a {
								font-size: 14px; } }
						h1 {
							font-size: 28px; }
						p {
							font-size: 16px;
 } } } } } } }							//line-height: 21px


