@include fortablet() {
	.mid-section {
		ul.nav {
			height: 0;
			overflow: hidden;
			@include transitionspeed(.3s);
			display: block;
			margin: 0;
			float: none;
			clear: both;

			li {
				float: none;
				//display: inline-block
				border-bottom: 1px $darkGray solid;
				margin: 0;
				&:first-of-type {
					border-top: 1px $darkGray solid; }

				a {
					line-height: 30px;
					display: block;
					padding: 5px 10px 0;
					background: $darkGrayDarken;

					&.active,
					&:focus {
						background: $red;
						color: $white; } } }

			&.show {
				margin: 0;
				clear: both;
				width: 100%;
				@include transitionspeed(.3s); } } }

	.fullCarouselBlock {
		.fullCarousel {
			.fc-content {
				.imgCaption {
					.b1000px {
						padding: 0 40px; } } } } } }
